/** @jsx jsx */
import { jsx } from '@emotion/core';
import { useContext, useCallback } from 'react';
import { GlobalContext } from 'src/stores';
import { Fab } from '@material-ui/core';
import { setLocalStorageItem } from 'src/utils';

const ThemeTypeToggleFab = ({ ...rest }) => {
  const { themeType, setThemeType } = useContext(GlobalContext);

  const toggleThemeType = useCallback(() => {
    let newThemeType = themeType;
    if (themeType === 'light') {
      newThemeType = 'dark';
    } else {
      newThemeType = 'light';
    }
    setThemeType(newThemeType);
    setLocalStorageItem('themeType', newThemeType);
  }, [setThemeType, themeType]);

  return (
    <Fab {...rest} onClick={toggleThemeType}>
      {themeType === 'light' ? <i className="fas fa-moon" /> : <i className="fas fa-sun" />}
    </Fab>
  );
};

export default ThemeTypeToggleFab;
